import {NgModule, ErrorHandler} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {IonicApp, IonicModule, IonicErrorHandler} from 'ionic-angular';
import {MyApp} from './app.component';

import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { CustomerPage } from '../pages/customer/customer';
import { CustomerPageModule } from '../pages/customer/customer.module';


import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {AngularFireModule} from 'angularfire2';
import {AngularFirestoreModule} from 'angularfire2/firestore';
import {NotificationsService} from "../services/notifications.service";
import {Firebase} from "@ionic-native/firebase";
import {FIREBASE_CONFIG} from "../config/firebase.config";

import { HttpModule } from '@angular/http';
import { PostProvider } from '../providers/post-provider';
import { IonicStorageModule } from '@ionic/Storage';

@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicStorageModule.forRoot(), 
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/add-customer/add-customer.module.ngfactory#AddCustomerPageModuleNgFactory', name: 'AddCustomerPage', segment: 'add-customer', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/customer/customer.module.ngfactory#CustomerPageModuleNgFactory', name: 'CustomerPage', segment: 'customer', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/show-customer/show-customer.module.ngfactory#ShowCustomerPageModuleNgFactory', name: 'ShowCustomerPage', segment: 'show-customer', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    AngularFirestoreModule,
    CustomerPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Firebase,
    NotificationsService,
    PostProvider,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {
}



